<template>
  <div class="course-sections">
    <!-- 页头（返回） -->
    <el-row>
      <el-col :span="18">
        <el-page-header
          content="课程内容"
          @back="goBack"></el-page-header>
      </el-col>
      <el-col :span="6">
        <el-button
          class="el-icon-plus"
          type="primary"
          size="medium"
          style="float: right;"
          @click="handleAddSection" >添加阶段</el-button>
      </el-col>
    </el-row>
    <!-- 课程内容 -->
    <el-card
      shadow="never">
      <el-tree
        :data="sections"
        :props="sectionProps"
        draggable
        :allow-drop="handleAllowDrop"
        @node-drop="handleNodeDrop"
        v-loading="isLoading" >
        <div class="inner"
          slot-scope="{ node, data }">
          <span>{{ node.label }}</span>
          <span v-if="data.sectionName">
            <el-button
              size="small"
              @click.stop="handleEditSection(data.id)">编辑</el-button>
            <el-button
              size="small"
              type="primary"
              @click.stop="handleAddLesson(data)">添加课时</el-button>
            <el-button
              size="small"
              @click.stop="handleSectionStateChange(data)">{{ data.status | formatStatus }}</el-button>
          </span>
          <span v-else>
            <el-button
              size="small"
              @click.stop="handleEditLesson(data)">编辑</el-button>
            <el-button
              size="small"
              type="success"
              @click.stop="handleUploadVideo(data)">上传视频</el-button>
            <el-button
              size="small"
              @click.stop="handleLessonStateChange(data)">{{ data.status | formatStatus }}</el-button>
          </span>
        </div>
      </el-tree>
    </el-card>
    <!-- 章节添加/编辑对话框 -->
    <el-dialog
      v-if="sectionsDialogVisible"
      title="章节信息"
      :visible.sync="sectionsDialogVisible"
      width="50%" >
      <create-or-edit-sections
        :is-edit="isEditSections"
        :course-id="courseId"
        :section-id="sectionId"
        @success="handleSectionSuccess"
        @cancel="handleSectionCancel"></create-or-edit-sections>
    </el-dialog>
    <!-- 课时添加/编辑对话框 -->
    <el-dialog
      v-if="lessonDialogVisible"
      title="课时信息"
      :visible.sync="lessonDialogVisible"
      width="50%" >
      <create-or-edit-lesson
        :is-edit="isEditLesson"
        :course-id="courseId"
        :section-id="sectionId"
        :lesson-id="lessonId"
        @success="handleSectionSuccess"
        @cancel="handleSectionCancel"></create-or-edit-lesson>
    </el-dialog>
    <!-- 章节/课时状态修改对话框 -->
    <el-dialog
      v-if="statusDialogVisible"
      title="状态信息"
      :visible.sync="statusDialogVisible"
      width="36%" >
      <status
        :course-id="courseId"
        :status="sectionStatus || lessonStatus"
        :section-id="sectionId"
        :lesson-id="lessonId"
        @success="handleStatusSuccess"
        @cancel="handleStatusCancel"></status>
    </el-dialog>
  </div>
</template>

<script>
import {
  getSectionAndLesson,
  createOrUpdateSection,
  createOrUpdateLesson
} from '@/services/course-section'
import CreateOrEditSections from './components/create-or-edit-sections'
import CreateOrEditLesson from './components/create-or-edit-lesson'
import Status from './components/status.vue'

export default {
  name: 'CourseSections',
  props: {
    courseId: {
      type: [Number, String],
      required: true
    }
  },
  components: {
    CreateOrEditSections,
    CreateOrEditLesson,
    Status
  },
  data () {
    return {
      sections: [],
      sectionProps: {
        label (data) {
          return data.sectionName || data.theme
        },
        children: 'lessonDTOS'
      },
      isLoading: false,
      sectionsDialogVisible: false,
      isEditSections: false,
      sectionId: '',
      lessonDialogVisible: false,
      isEditLesson: false,
      lessonId: '',
      statusDialogVisible: false,
      sectionStatus: 0,
      lessonStatus: 0
    }
  },
  created () {
    this.loadSectionAndLesson()
  },
  methods: {
    async loadSectionAndLesson () {
      const { data } = await getSectionAndLesson(this.courseId)
      if (data.code === '000000') {
        this.sections = data.data
      }
    },
    handleAddSection () {
      this.sectionsDialogVisible = true
      this.isEditSections = false
      this.sectionId = ''
    },
    handleEditSection (sectionId) {
      this.sectionsDialogVisible = true
      this.isEditSections = true
      this.sectionId = sectionId
    },
    handleSectionSuccess () {
      this.sectionsDialogVisible = false
      this.lessonDialogVisible = false
      this.sectionId = ''
      this.lessonId = ''
      this.loadSectionAndLesson()
    },
    handleSectionCancel () {
      this.sectionsDialogVisible = false
      this.lessonDialogVisible = false
      this.sectionId = ''
      this.lessonId = ''
    },
    handleSectionStateChange (section) {
      this.statusDialogVisible = true
      this.sectionStatus = section.status
      this.sectionId = section.id
    },
    handleStatusSuccess () {
      this.statusDialogVisible = false
      this.sectionId = ''
      this.lessonId = ''
      this.loadSectionAndLesson()
    },
    handleStatusCancel () {
      this.statusDialogVisible = false
      this.sectionId = ''
      this.lessonId = ''
    },
    handleAddLesson (section) {
      this.lessonDialogVisible = true
      this.isEditLesson = false
      this.sectionId = section.id
      this.lessonId = ''
    },
    handleEditLesson (lesson) {
      this.lessonDialogVisible = true
      this.isEditLesson = true
      this.sectionId = lesson.sectionId
      this.lessonId = lesson.id
    },
    handleLessonStateChange (lesson) {
      this.statusDialogVisible = true
      this.lessonStatus = lesson.status
      this.sectionId = lesson.sectionId
      this.lessonId = lesson.id
    },
    handleUploadVideo (lesson) {
      this.$router.push({
        name: 'course-video',
        params: {
          courseId: this.courseId,
          lessonId: lesson.id
        }
      })
    },
    handleAllowDrop (draggingNode, dropNode, type) {
      return type !== 'inner' && (draggingNode.data.sectionId === dropNode.data.sectionId)
    },
    async handleNodeDrop (draggingNode, dropNode, type, event) {
      try {
        this.isLoading = true
        await Promise.all(dropNode.parent.childNodes.map((item, index) => {
          if (draggingNode.data.sectionId) {
            createOrUpdateLesson({
              id: item.data.id,
              orderNum: index
            })
          } else {
            createOrUpdateSection({
              id: item.data.id,
              orderNum: index
            })
          }
        }))
        this.$message.success('数据更新成功')
      } catch (error) {
        this.$message.error('数据更新失败')
      } finally {
        this.isLoading = false
      }
    },
    goBack () {
      this.$router.push({
        name: 'course'
      })
    }
  },
  filters: {
    formatStatus (status) {
      const statuses = ['已隐藏', '待更新', '已更新']
      return statuses[status]
    }
  }
}
</script>

<style lang="scss" scoped>
.course-sections {
  .el-page-header {
    margin-bottom: 20px;
  }
  .inner {
    display: flex;
    flex: 1;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #ebeef5;
  }
  ::v-deep .el-tree-node__content {
    height: auto;
  }
}
</style>
