<template>
  <div class="create-or-edit-sections">
    <el-form
      label-width="80px"
      :model="section"
      :rules="sectionFormRules">
      <el-form-item
        label="课程名称">
        <el-input
          v-model="course.courseName"
          disabled ></el-input>
      </el-form-item>
      <el-form-item
        label="章节名称"
        prop="sectionName">
        <el-input
          v-model="section.sectionName"></el-input>
      </el-form-item>
      <el-form-item
        label="章节描述">
        <el-input
          v-model="section.description"
          type="textarea" ></el-input>
      </el-form-item>
      <el-form-item
        label="章节排序">
        <el-input
          v-model="section.orderNum"
          type="number"
          :min=0 >
          <template slot="append">数字控制排序，数字越大越靠后</template>
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          size="medium"
          @click="handleCancel">取消</el-button>
        <el-button
          size="medium"
          type="primary"
          @click="handleSubmit">确定</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {
  getCourseById
} from '@/services/course'
import {
  getSectionById,
  createOrUpdateSection
} from '@/services/course-section'

export default {
  name: 'CreateOrEditSections',
  props: {
    isEdit: {
      type: Boolean,
      default: false
    },
    sectionId: [Number, String],
    courseId: [Number, String]
  },
  data () {
    return {
      course: {},
      section: {},
      sectionFormRules: {
        sectionName: [
          { required: true, message: '请输入章节名称', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.loadCourseInfo()
    if (this.isEdit) {
      this.loadSectionInfo()
    }
  },
  methods: {
    async loadCourseInfo () {
      const { data } = await getCourseById(this.courseId)
      if (data.code === '000000') {
        this.course = data.data
      }
    },
    async loadSectionInfo () {
      const { data } = await getSectionById(this.sectionId)
      if (data.code === '000000') {
        this.section = data.data
      }
    },
    handleCancel () {
      this.$emit('cancel')
    },
    async handleSubmit () {
      const { data } = await createOrUpdateSection({
        id: this.section.id,
        courseId: this.courseId,
        courseName: this.course.courseName,
        sectionName: this.section.sectionName,
        description: this.section.description,
        orderNum: this.section.orderNum
      })
      if (data.code === '000000') {
        this.$emit('success')
        this.$message.success('提交成功')
      }
    }
  }
}
</script>

