<template>
  <div class="create-or-edit-lesson">
    <el-form
      label-width="120px"
      :model="lesson"
      :rules="lessonFormRules">
      <el-form-item
        label="课程名称">
        <el-input
          v-model="course.courseName"
          disabled ></el-input>
      </el-form-item>
      <el-form-item
        label="章节名称">
        <el-input
          v-model="section.sectionName"
          disabled></el-input>
      </el-form-item>
      <el-form-item
        label="课时名称"
        prop="theme">
        <el-input
          v-model="lesson.theme"></el-input>
      </el-form-item>
      <el-form-item
        label="时长"
        prop="duration" >
        <el-input
          v-model="lesson.duration"
          type="number"
          :min="0">
          <template slot="append">分钟</template>
        </el-input>
      </el-form-item>
      <el-form-item
        label="是否开放试听"
        prop="isFree">
        <el-switch
          v-model="lesson.isFree"></el-switch>
      </el-form-item>
      <el-form-item
        label="章节排序">
        <el-input
          v-model="lesson.orderNum"
          type="number"
          :min=0 >
          <template slot="append">数字控制排序，数字越大越靠后</template>
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          size="medium"
          @click="handleCancel">取消</el-button>
        <el-button
          size="medium"
          type="primary"
          @click="handleSubmit">确定</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {
  getCourseById
} from '@/services/course'
import {
  getSectionById,
  getLessonById,
  createOrUpdateLesson
} from '@/services/course-section'

export default {
  name: 'CreateOrEditLesson',
  props: {
    isEdit: {
      type: Boolean,
      default: false
    },
    courseId: {
      type: [Number, String],
      required: true
    },
    sectionId: {
      type: [Number, String],
      required: true
    },
    lessonId: [Number, String]
  },
  data () {
    return {
      course: {},
      section: {},
      lesson: {
        isFree: false
      },
      lessonFormRules: {
        theme: [
          { required: true, message: '请输入课程名称', trigger: 'blur' }
        ],
        duration: [
          { required: true, message: '请输入课程时长', trigger: 'blur' }
        ],
        isFree: [
          { required: true, message: '请选择是否开启试听', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.loadCourseInfo()
    this.loadSectionInfo()
    if (this.isEdit) {
      this.loadLessonInfo()
    }
  },
  methods: {
    async loadCourseInfo () {
      const { data } = await getCourseById(this.courseId)
      if (data.code === '000000') {
        this.course = data.data
      }
    },
    async loadSectionInfo () {
      const { data } = await getSectionById(this.sectionId)
      if (data.code === '000000') {
        this.section = data.data
      }
    },
    async loadLessonInfo () {
      const { data } = await getLessonById(this.lessonId)
      if (data.code === '000000') {
        this.lesson = data.data
      }
    },
    handleCancel () {
      this.$emit('cancel')
    },
    async handleSubmit () {
      const { data } = await createOrUpdateLesson({
        id: this.lesson.id,
        courseId: this.courseId,
        sectionId: this.sectionId,
        theme: this.lesson.theme,
        duration: this.lesson.duration,
        isFree: this.lesson.isFree,
        orderNum: this.section.orderNum
      })
      if (data.code === '000000') {
        this.$emit('success')
        this.$message.success('提交成功')
      }
    }
  }
}
</script>

