import request from '@/utils/request'

// 获取课程的章节内容和课时信息
export const getSectionAndLesson = courseId => {
  return request({
    method: 'get',
    url: '/boss/course/section/getSectionAndLesson',
    params: {
      courseId
    }
  })
}

// 获取章节信息
export const getSectionById = sectionId => {
  return request({
    method: 'get',
    url: '/boss/course/section/getBySectionId',
    params: {
      sectionId
    }
  })
}

// 保存或更新章节内容
export const createOrUpdateSection = data => {
  return request({
    method: 'post',
    url: '/boss/course/section/saveOrUpdateSection',
    data
  })
}

// 根据ID获取课时信息
export const getLessonById = lessonId => {
  return request({
    method: 'get',
    url: '/boss/course/lesson/getById',
    params: {
      lessonId
    }
  })
}

// 保存或更新课时内容
export const createOrUpdateLesson = data => {
  return request({
    method: 'post',
    url: '/boss/course/lesson/saveOrUpdate',
    data
  })
}
