<template>
  <div class="status">
    <i class="el-icon-warning" style="margin-bottom: 20px;"></i>
    <span style="font-size: 24px; margin-bottom: 30px;">当前状态：{{ status | formatStatus }}</span>
    <el-form
      label-width="100px">
      <el-form-item
        label="状态变更为："
        style="margin-bottom: 40px;">
        <el-select
          v-model="newStatus">
          <el-option label="已隐藏" :value="0"></el-option>
          <el-option label="待更新" :value="1"></el-option>
          <el-option label="已更新" :value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button
          @click="handleCancel">取消</el-button>
        <el-button
          type="primary"
          @click="handleSubmit">确定</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {
  createOrUpdateSection,
  createOrUpdateLesson
} from '@/services/course-section'

export default {
  name: 'Status',
  props: {
    status: [Number, String],
    courseId: [Number, String],
    sectionId: [Number, String],
    lessonId: [Number, String]
  },
  data () {
    return {
      newStatus: this.status
    }
  },
  methods: {
    async handleSubmit () {
      if (this.lessonId) {
        try {
          await createOrUpdateLesson({
            id: this.lessonId,
            courseId: this.courseId,
            sectionId: this.sectionId,
            status: this.newStatus
          })
          this.$message.success('提交成功')
          this.$emit('success')
        } catch (error) {
          this.$message.error('提交失败')
        }
        return
      }
      if (this.sectionId) {
        try {
          await createOrUpdateSection({
            id: this.sectionId,
            courseId: this.courseId,
            status: this.newStatus
          })
          this.$message.success('提交成功')
          this.$emit('success')
        } catch (error) {
          this.$message.error('提交失败')
        }
      }
    },
    handleCancel () {
      this.$emit('cancel')
    }
  },
  filters: {
    formatStatus (status) {
      const statuses = ['已隐藏', '待更新', '已更新']
      return statuses[status]
    }
  }
}
</script>

<style lang="scss" scoped>
.status {
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  .el-icon-warning:before {
    display: inline-block;
    width: 60px;
    height: 60px;
    font-size: 60px;
  }
}
</style>
